.navbar {
  overflow: hidden;
  background-color: #333;
}

.UpperBar {
  display: flex;
}

.UpperBar li {
  list-style: none;
}

.Logo {
  width: 50px;
  padding-left: 1%;
  padding-top: 5px;
}

.SearchBar {
  margin-top: 10px;
  margin-bottom: 10px;
  margin-left: 28%;
  margin-right: 20%;
}

.Welcome {
  margin: 0 auto;
}

/* Nav links */
.navbar a {
  float: left;
  font-size: 16px;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  margin-left: 50px;
}

.subnav {
  float: left;
  overflow: hidden;
}

.subnav .subnavbtn {
  font-size: 16px;
  border: none;
  outline: none;
  color: white;
  padding: 14px 16px;
  background-color: inherit;
  font-family: inherit;
  margin: 0;
  margin-left: 50px;
}

/* Adds a background color to nav links on hover */
.navbar a:hover,
.subnav:hover .subnavbtn {
  background-color: rgb(90, 90, 90);
}

.subnav-content {
  display: none;
  position: absolute;
  left: 0;
  background-color: rgb(90, 90, 90);
  width: 100%;
  z-index: 1;
}

.subnav-content a {
  float: left;
  color: white;
  text-decoration: none;
}

/* Adds a grey background color on hover */
.subnav-content a:hover {
  background-color: #eee;
  color: black;
}

/* Open the drop down menus */
.subnav:hover .subnav-content {
  display: block;
}

